<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="fiexdHeader">
      <div slot="left" color="#3051f1">
        <a href="#/Navigation?id=2">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">数据录入</span>
      </div>
      <div slot="right" color="#3051f1">
        <mu-button icon size="20">
          <mu-icon color="white" value="add"></mu-icon>
        </mu-button>
      </div>
    </mu-appbar>
    <div class="backWhite" style="margin-top:70px;">
      <div class>
        <mu-row style="padding:0 10px;height:40px;">
          <mu-col span="3" lg="4" sm="6" class="flexCenterMainChild">监测时间</mu-col>
          <mu-col span="9" lg="4" sm="6">
            <mu-date-input
              v-model="value3"
              type="dateTime"
              container="bottomSheet"
              label
              label-float
              full-width
              no-display
            >
              <!-- <mu-icon value=""></mu-icon> -->
            </mu-date-input>
          </mu-col>
        </mu-row>
        <div class="flexList">
          <div>
            <span>点位名称</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);"></span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>经纬度</span>
          </div>
          <div class="inputDivContent">
            <input type="text" @focus="clickToMap()" id="input" v-model="LngLatInputValue" />
            <span style="color: rgb(158, 156, 156);"></span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>pH</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);"></span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>COD</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总氮</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总铜</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>氨氮</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总镍</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总磷</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总铬</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
        <div class="flexList">
          <div>
            <span>总氰化合物</span>
          </div>
          <div class="inputDivContent">
            <input type="text" id="input" v-model="pHInputValue" />
            <span style="color: rgb(158, 156, 156);">mg/L</span>
          </div>
        </div>
      </div>
    </div>
    <div>
      <div class="HeaderTitle">
        <div>
          <span>现场图片</span>
        </div>
      </div>
      <div class="HeaderTitleContent">
        <el-upload
          ref="upload"
          action="http://47.112.16.168:8803/explore"
          multiple
          name="files"
          list-type="picture-card"
          :on-change="handleRemove"
          :http-request="uploadFile"
          :auto-upload="false"
          :limit="1"
          :on-exceed="limitStatus"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "controlListDetails",
  data() {
    return {
      value3: "",
      pHInputValue: "",
      LngLatInputValue:"",
      listContent: [],
      list: [],
      token: "",
      panel: "",
      dialogImageUrl: "",
      dialogVisible: false
    };
  },
  methods: {
    limitStatus(e) {
      this.$toast.warning("只能上传一个视频");
      console.log(e);
    },

    uploadFile(res) {
      this.formDate.append("filePhoto", res.file);
    },
    handleRemove(file, fileList) {},
    clickToMap() {
      console.log(1);
      this.$router.replace("/selectMap");
    },
    toggle(panel) {
      this.panel = panel === this.panel ? "" : panel;
    },
    async initData() {
      var url = "/job/operate/list";
      var res = await AppBace.request(url, "GET", {}, this.token);
      console.log(res);
      this.listContent = res.data.data.rows;
      for (let index = 0; index < this.listContent.length; index++) {
        const element = this.listContent[index];
        this.queryId(element);
      }
    },
    queryId(e) {
      var id = this.$route.query.name;

      if (e.operateId === id) {
        return this.list.push(e);
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.LngLatInputValue = this.$route.query.id;
    // this.LngLatInputValue = sessionStorage.getItem("lnglat");
    // this.initData();
  }
};
</script>
<style scoped>
.flexList {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px rgb(241, 240, 240) solid;
  align-items: center;
}
.backWhite {
  background: white;
  margin: 5px;
  border-radius: 10px;
}
.flex {
  display: flex;
  justify-content: center;
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
.input {
  /* display: flex;
    flex: 1; */
  width: 100%;
}
.inputDivContent input {
  width: 80%;
  outline: none;
  /* border: #ccc 1px solid;
    border-radius: 5px; */
  padding: 0 5px;
  border: none;
  height: 30px;
  border-radius: 5px;
}
.inputDivContent {
  /* width: 100%; */
  /* justify-content: right; */
  /* flex: 1; */
  display: flex;
  width: 80%;
  flex-direction: row;
  align-items: center;
  border: #ccc 1px solid;
  border-radius: 5px;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.HeaderTitle {
  font-size: 16px;
  margin-left: 10px;
  border-left: #feb925 5px solid;
  padding-left: 5px;
  margin-bottom: 10px;
}
.HeaderTitleContent {
  margin-left: 40px;
  /* overflow: hidden; */
}
</style>



